import React, { useState, useEffect } from 'react';
import { makeStyles, } from '@material-ui/core/styles';

import * as Yup from 'yup'
//import './student.css'
import Container from '@material-ui/core/Container';
import {
    Grid, Paper, TextField,
    FormControlLabel, Button,
    Typography, FormControl
} from '@material-ui/core'
import { Formik, Form, ErrorMessage } from 'formik'
import SaveIcon from '@material-ui/icons/Save';
import { Autocomplete } from '@material-ui/core';

import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';
import { FormHelperText } from '@material-ui/core'
import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';

import AdapterDateFns from '@material-ui/lab/AdapterDateFns';
import LocalizationProvider from '@material-ui/lab/LocalizationProvider';
import DesktopDatePicker from '@material-ui/lab/DesktopDatePicker';
import Stack from '@material-ui/core/Stack';
import { format } from "date-fns";
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Swal from 'sweetalert2'
import MuiAlert from '@material-ui/core/Alert';

import { requesicoes } from "../../../utils/services";
import Input from '../../../ui-component/shared/Input';
import { useParams, useLocation } from 'react-router-dom';
import CryptoJS from 'crypto-js';

const useStyles = makeStyles((theme) => ({
    root: {
        '&:hover': {
            backgroundColor: 'transparent',
        },
    },

}));


let idDesc = null
let sexos = [];
let documentos = [];
let provincias = [];
let nacionalidades = [];
let distritos = [];
let cidades = []
let cursos = [];
let provinciasde = [];
let estudante = {};
let especializacoes = [];
let curriculos = [];

const NovoEstudante = () => {

    const classes = useStyles();
    const [initialValues, setInitialValues] = useState({});
    const [updateDOM, setUpdateDOM] = useState(false);
    const [provincia, setProvincia] = useState('');
    const [selectedDateNasc, setSelectedDateNasc] = useState(new Date());
    const [selectedDateEmissao, setSelectedDateEmissao] = useState(new Date());
    const [isLoadingData, setIsLoadingData] = useState(true);
    const [redirect, setRedirect] = useState(false);
    const [to, setTo] = useState('');
    const [open, setOpen] = React.useState(false);
    const [isCampoHabilitado, setIsCampoHabilitado] = useState(false);
    const [mostrarMensagem, setMostrarMensagem] = useState(false);
    const [carregando, setCarregando] = useState(true);
    //  // Use useLocation

    const location = useLocation();
    const encryptedParam = new URLSearchParams(location.search).get('id');
    console.log('===========Chegou aqui com id useParam: ', encryptedParam);

    const handleClick = () => {
        setOpen(true);
    };

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }

        setOpen(false);
    };

    const action = (
        <React.Fragment>
            <Button color="secondary" size="small" onClick={handleClose}>
                UNDO
            </Button>
            <IconButton
                size="small"
                aria-label="close"
                color="inherit"
                onClick={handleClose}
            >
                <CloseIcon fontSize="small" />
            </IconButton>
        </React.Fragment>
    );

    useEffect(() => {
        console.log('Antes de desencriptar ', encryptedParam);
        idDesc = decryptParam(encryptedParam);
        console.log('Depois de desencriptar ', idDesc);
        setTimeout(() => {
            setCarregando(false);
        }, 3000);
        init();
    }, [])

    const decryptParam = (encryptedParam) => {
        var myPassword = "ciam2023";
        var decrypted = CryptoJS.AES.decrypt(encryptedParam, myPassword);
        return decrypted.toString(CryptoJS.enc.Utf8);
    };

    useEffect(() => {
        // Inicie um timeout de 5 segundos para mostrar o aviso
        const timeoutID = setTimeout(() => {
            setMostrarMensagem(true);
        }, 5000);

        // Lembre-se de limpar o timeout quando o componente for desmontado
        return () => clearTimeout(timeoutID);
    }, []);


    const handleDateChange = (date) => {
        //setSelectedDate(date);
        setSelectedDateNasc(date);
        setSelectedDateEmissao(date)
    };

    const paperStyle = { padding: '50px 25px', width: 750, margin: ' auto' }


    async function init() {

        const [requestEstudante, requestSexo, tiposDocumentos, listaCurso, listaPaises, listaprovincias, listaCidades, especializacaoRequest]
            = await Promise.all([requesicoes().getestudanteById(`/${idDesc}?campos=id,nome,numero,apelido,mae,pai,sexo,dataNascimento,email,telemovel,nacionalidade,cidade,naturalidade,tipoDocumento,numeroDocumento,dataEmissao,curriculo.id,curriculo.ano,curso.id,curso.nome`), requesicoes().buscarSexo(),
            requesicoes().buscarDocumentoEst(), requesicoes().buscarCurso('?campos=id,nome'), requesicoes().buscarPaises(), requesicoes().buscarProvincia('?campos=id,name,country_id.id'),
            requesicoes().buscarCidades('?campos=id,name,regiaoPai.id,regiaoPai.name'), requesicoes().getEspecilizacao('?campos=id,nome,curso.id,curso.nome')])

        // nacionalidade = listaPaises.data
        sexos = requestSexo.data
        documentos = tiposDocumentos.data
        provincias = listaprovincias.data
        distritos = listaCidades.data
        nacionalidades = listaPaises.data
        cursos = listaCurso.data
        estudante = requestEstudante.data
        especializacoes = especializacaoRequest.data

        console.log("===Estudante: ", estudante);

        setInitialValues({ ...estudante });

        setIsLoadingData(false);
        setUpdateDOM(!updateDOM)

    }

    async function buscarProvincias(v) {
        provinciasde = provincias.filter(p => p.country_id.id == v.id)
        setProvincia(provinciasde)

    }

    async function buscarCidades(v) {
        cidades = distritos.filter(d => d.regiaoPai.id == v.id)
        //setProvincia(cidades)
    }

    /*async function buscarCurrrriculos(v) {

        const listaCurriculos = await requesicoes().buscarCurriculos(v.id);
        curriculos = listaCurriculos.data

    }*/

    const validationSchema = Yup.object().shape({
        apelido: Yup.string().min(3, "Deve ter no minio 3 caratere").required("Obrigatorio"),
        nome: Yup.string().min(3, "Deve ter no minio 3 caratere").required("Campo Obrigatorio"),
        sexo: Yup.string().oneOf(["Masculino", "Femenino"], "Required").required("Campo Obrigatorio"),
        pai: Yup.string().min(3, "Deve ter no minio 3 carateres").required('Campo Obrigatorio'),
        mae: Yup.string().min(3, "Deve ter no minio 3 carateres").required("Campo Obrigatorio")
    })




    return (<Container className="AppNovoEstudante">



        <Paper elevation={5} style={paperStyle}>
            {!isLoadingData ?
                <div>
                    <Grid align='left'>
                        <Typography variant='h4'>Alterar dados do Estudante</Typography>
                        <Typography variant='caption'>Prencha os dados para alterar dados do estudante</Typography>
                    </Grid>
                    <br></br>

                    <Formik initialValues={initialValues} validationSchema={validationSchema}

                        onSubmit={(values, { setSubmitting }) => {
                            async function save(values) {

                                //console.log(JSON.stringify(values.id), 'Dados')

                                try {

                                    await Promise.resolve(requesicoes().putEstudantes(values));
                                    //setOpen(true)
                                    Swal.fire(
                                        'Exito!',
                                        'Dados do Estudante atualizado com sucesso!',
                                        'success'
                                    )
                                    setTo('/student/list');
                                    setRedirect(true);


                                } catch (e) {

                                    if (e.response.status == 400) {
                                        Swal.fire({
                                            icon: 'error',
                                            title: 'Oops...',
                                            text: e.response.data.map((m, i) => m.mensagemUsuario),

                                        })

                                    } else {

                                        Swal.fire({
                                            icon: 'error',
                                            title: 'Oops...',
                                            text: e.response.data.message,
                                        })

                                    }

                                }
                            }
                            save(values);
                            setTimeout(() => {
                                setSubmitting(false);
                            }, 500);
                        }}>
                        {(props) => {
                            const { values, touched, errors, dirty, isSubmitting,
                                handleChange, handleBlur, handleSubmit, handleReset, setFieldValue } = props;
                            return (
                                <Form>

                                    <Grid container spacing={2} >
                                        <Grid item xs={12} sm={6} className="AppInput">
                                            {/* <TextField
                                                type="text" variant="outlined" name="apelido"
                                                error={(errors.apelido && touched.apelido)}
                                                helperText={errors.apelido}
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.apelido} margin="dense"
                                                id="apelido" fullWidth label="Apelido" /> */}
                                            <Input name="apelido" label="Apelido"
                                                placeholder="Digite ltimo nome"
                                                helperText={<ErrorMessage name='apelido' required fullWidth />}
                                                error={props.errors.apelido && props.touched.apelido} />
                                        </Grid>
                                        <Grid item xs={12} sm={6} className="AppInput">
                                            <Input name="nome"
                                                label="Nome completo (Sem incluir o apelido)"
                                                placeholder="Digite nome completo"
                                                helperText={<ErrorMessage name='nome' />}
                                                error={props.errors.nome && props.touched.nome} />
                                        </Grid>
                                        <Grid item xs={12} sm={6} className="AppInput">
                                            <Input name="pai" label="Pai(Filiao)"
                                                placeholder="Digite nome do pai"
                                                helperText={<ErrorMessage name='[pai]' />}
                                                error={props.errors.pai && props.touched.pai} /></Grid>

                                        <Grid item xs={12} sm={6} className="AppInput">
                                            <Input name="mae" label="Me (Filiao)"
                                                placeholder="Digite nome da me"
                                                helperText={<ErrorMessage name='mae' />}
                                                error={props.errors.mae && props.touched.mae} />
                                        </Grid>
                                        <Grid item xs={12} sm={6} className="AppInput">

                                            <FormControl component="fieldset" >
                                                <Typography variant="body1" gutterBottom>Sexo</Typography>
                                                <RadioGroup row aria-label="sexo" name="sexo" value={values.sexo}
                                                    onChange={(e) => { setFieldValue('sexo', e.target.value); }}>
                                                    {sexos.map(opt =>
                                                        <FormControlLabel key={opt} value={opt} control={<Radio />} label={opt} />)}
                                                </RadioGroup>
                                                <FormHelperText></FormHelperText>
                                            </FormControl>

                                        </Grid>

                                        <Grid item xs={12} sm={6} className="AppInput">


                                            <LocalizationProvider dateAdapter={AdapterDateFns}>
                                                <Stack spacing={3}>

                                                    <DesktopDatePicker
                                                        label="Data de Nascimento"
                                                        name="dataNascimento"
                                                        format="dd/MM/yyyy"
                                                        value={values.dataNascimento}

                                                        // minDate={new Date('2017-01-01')}
                                                        onChange={(v) => {
                                                            setSelectedDateNasc(v);
                                                            setFieldValue('dataNascimento', format(v, 'yyyy-MM-dd'))
                                                        }}
                                                        KeyboardButtonProps={{
                                                            'aria-label': 'change date',
                                                        }}

                                                        renderInput={(params) => <TextField {...params} />}
                                                    />

                                                </Stack>
                                            </LocalizationProvider>
                                        </Grid>

                                        <Grid item xs={12} sm={6} className="AppInput">
                                            <Autocomplete name="nacionalidade"
                                                onChange={(e, v) => {
                                                    setFieldValue('nacionalidade', v);
                                                    buscarProvincias(v)
                                                }} value={values.nacionalidade}
                                                fullWidth id="nacionalidade"
                                                getOptionLabel={option => option.name}
                                                options={nacionalidades} style={{ minWidth: 200 }}
                                                renderInput={(params) => <TextField variant="outlined" margin="dense" label="Nacionalidade"
                                                    fullWidth {...params} placeholder="Seleccione uma opo" />}
                                            />

                                        </Grid>
                                        <Grid item xs={12} sm={6} className="AppInput">

                                            <Autocomplete name="naturalidade"
                                                onChange={(e, v) => {
                                                    setFieldValue('naturalidade', v);
                                                    buscarCidades(v)
                                                }} value={values.naturalidade}
                                                id="controllable-states-demo"
                                                getOptionLabel={option => option.name}
                                                options={provinciasde}
                                                // style={{ width: 328 }}
                                                renderInput={(params) => <TextField {...params}
                                                    InputLabelProps={{
                                                        shrink: true,
                                                    }} placeholder="--Selecione--"
                                                    label="Provncia" variant="outlined" />}
                                            />
                                        </Grid>
                                        <Grid item xs={12} sm={6} className="AppInput">
                                            <Autocomplete name="cidade"
                                                onChange={(e, v) => { setFieldValue('cidade', v); }}
                                                id="controllable-states-demo" value={values.cidade}
                                                getOptionLabel={option => option.name}
                                                options={cidades}
                                                // style={{ width: 328 }}
                                                renderInput={(params) => <TextField {...params}
                                                    InputLabelProps={{
                                                        shrink: true,
                                                    }} placeholder="--Selecione--"
                                                    label="Cidade" variant="outlined" />}
                                            />
                                        </Grid>

                                        <Grid item xs={12} sm={6} >
                                            <FormControl component="fieldset" >
                                                <Typography variant="body1" gutterBottom>Tipo de Documento</Typography>

                                                <RadioGroup row aria-label="tipoDocumento" name="tipoDocumento" value={values.tipoDocumento}
                                                    onChange={(e) => { setFieldValue('tipoDocumento', e.target.value); }}>
                                                    {documentos.map(opt =>
                                                        <FormControlLabel key={opt} value={opt.values} control={<Radio />} label={opt.label} />)}
                                                </RadioGroup>
                                                <FormHelperText></FormHelperText>
                                            </FormControl>


                                        </Grid>
                                        <Grid item xs={12} sm={6} className="AppInput">
                                            <Input name="numeroDocumento" label="N de identificao"
                                                placeholder="Digite o nmero "
                                                helperText={<ErrorMessage name='apelido' />}
                                                error={props.errors.apelido && props.touched.apelido} />
                                        </Grid>

                                        <Grid item xs={12} sm={6} className="AppInput">
                                            <LocalizationProvider dateAdapter={AdapterDateFns}>
                                                <Stack spacing={3}>

                                                    <DesktopDatePicker
                                                        label="Data de Emissao"
                                                        name="dataEmissao"
                                                        format="dd/MM/yyyy"
                                                        // value={selectedDateEmissao}
                                                        value={values.dataEmissao}
                                                        // minDate={new Date('2017-01-01')}
                                                        onChange={(v) => {
                                                            setSelectedDateEmissao(v);
                                                            setFieldValue('dataEmissao', format(v, 'yyyy-MM-dd'))
                                                        }}
                                                        KeyboardButtonProps={{
                                                            'aria-label': 'change date',
                                                        }}

                                                        renderInput={(params) => <TextField {...params} />}
                                                    />

                                                </Stack>
                                            </LocalizationProvider>

                                        </Grid>
                                        <Grid item xs={12} sm={6} className="AppInput">
                                            <Input name="telemovel" label="Telemvel"
                                                placeholder="Digite o nmero "
                                            />
                                        </Grid>
                                        <Grid item xs={12} sm={6} className="AppInput">
                                            <Input name="email" label="E-mail"
                                                placeholder="Digite o e-mail "
                                            />
                                        </Grid>
                                        <Grid item xs={12} sm={6} className="AppInput">
                                            <Autocomplete
                                                name="curso"
                                                onChange={(e, v) => {
                                                    setFieldValue('curso', v);
                                                    // buscarCurrrriculos(v)
                                                    if (especializacoes.filter(e => e.curso.id === v.id).length > 0) {
                                                        console.log(v.nome, 'Entrou aqui')
                                                        setIsCampoHabilitado(true);
                                                    } else {
                                                        setIsCampoHabilitado(false);
                                                    }
                                                }} value={values.curso}
                                                id="controllable-states-demo"
                                                getOptionLabel={option => option.nome}
                                                options={cursos}
                                                // style={{ width: 328 }}
                                                renderInput={(params) => <TextField {...params}
                                                    InputLabelProps={{
                                                        shrink: true,
                                                    }} placeholder="--Selecione--"
                                                    label="Curso" variant="outlined" />}
                                            />
                                        </Grid>

                                        <Grid item xs={12} sm={6} className="AppInput">
                                            <Autocomplete
                                                name="curriculo" value={values.curriculo}
                                                onChange={(e, v) => {
                                                    setFieldValue('curriculo', v);

                                                }}
                                                id="controllable-states-demo"
                                                getOptionLabel={option => option.ano}
                                                options={curriculos}
                                                // style={{ width: 328 }}
                                                renderInput={(params) => <TextField {...params}
                                                    InputLabelProps={{
                                                        shrink: true,
                                                    }} placeholder="--Selecione--"
                                                    label="Curriculo" variant="outlined" />}
                                            />

                                        </Grid>
                                        {isCampoHabilitado && (
                                            <Grid item xs={12} sm={6} className="AppInput">
                                                <Autocomplete
                                                    name="especializacao" value={values.especializacao}
                                                    onChange={(e, v) => {
                                                        setFieldValue('especializacao', v);

                                                    }}
                                                    id="controllable-states-demo"
                                                    getOptionLabel={option => option.nome}
                                                    options={especializacoes}
                                                    renderInput={(params) => <TextField {...params}
                                                        InputLabelProps={{
                                                            shrink: true,
                                                        }} placeholder="--Selecione--"
                                                        label="Especializacao" variant="outlined" />}
                                                />
                                            </Grid>

                                        )}


                                        {/* <Grid item xs={12} sm={6} className="AppInput">
                                    <Autocomplete
                                        name="curiculo"
                                        onChange={(e, v) => {
                                            setFieldValue('curiculo', v);
                                            
                                        }}
                                        id="controllable-states-demo"
                                        getOptionLabel={option => option.nome}
                                        options={curriculos}
                                        // style={{ width: 328 }}
                                        renderInput={(params) => <TextField {...params}
                                            InputLabelProps={{
                                                shrink: true,
                                            }} placeholder="--Selecione--"
                                            label="Curriculo" variant="outlined" />}
                                    />

                                </Grid> */}

                                        <Grid item xs={12} sm={12}>
                                            <Button variant="contained" color="primary" type='submit' size="large"
                                                className={classes.button} startIcon={<SaveIcon />}>
                                                Actualizar
                                            </Button>
                                        </Grid>



                                    </Grid>


                                </Form>)
                        }}
                    </Formik>
                </div> : <div>
                    {!mostrarMensagem ? (
                        <div>Processando!</div>
                    ) : (
                        <Alert severity="warning">
                            <AlertTitle>Tempo de Espera Excedido</AlertTitle>
                            Deveria mostrar alguma informao. Por favor, avise o <strong>CIAM</strong> sobre este problema.
                        </Alert>

                    )}
                </div>}
        </Paper>

    </Container >
    );

}
export default NovoEstudante
